/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 80%;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

section {
  padding: 2rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

a {
  text-decoration: none;
}

.header {
  background: #000;
  height: 130vh;
  text-align: center;
}
.header__busqueda {
  margin-top: 5rem;
}
.header .header__busqueda-boton {
  background-color: #FFFFFF;
  padding: 2rem 4rem;
  color: #e8505b;
  margin-top: 1rem;
  border-radius: 3rem;
  font-size: 2rem;
  font-weight: 700;
  display: block;
}
@media (min-width: 768px) {
  .header .header__busqueda-boton {
    display: inline-block;
  }
}

.barra {
  overflow: hidden;
}
@media (min-width: 768px) {
  .barra__contenedor {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 0;
  }
}
.barra--fijar {
  display: flex;
  background-color: #FFFFFF;
  height: fit-content;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  transition: 0.3s ease all;
}
.barra__navegacion {
  display: flex;
  gap: 4rem;
}
.barra__link {
  color: #FFFFFF;
  margin: 0;
}
.barra__logo {
  height: 5rem;
  width: 20rem;
  background-image: url("../img/airbnb_logo.svg");
  background-repeat: no-repeat;
}
.barra__logo:hover {
  background-image: url("../img/airbnb_logo_blanco.svg");
  cursor: pointer;
}
.barra--modificar {
  width: 80%;
  margin: 0 auto;
  overflow: visible;
}
.barra--modificar .barra__navegacion {
  display: none;
}
.barra--modificar .barra__busqueda {
  display: flex;
}
@media (min-width: 1024px) {
  .barra--modificar .barra__link {
    color: #000;
    margin: 0;
  }
  .barra--modificar .barra__logo:hover {
    background-image: url("../img/airbnb_logo_oscuro.svg");
  }
}
.barra__busqueda {
  display: none;
  align-items: center;
  border-radius: 15rem;
  background-color: #FFFFFF;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.4);
}
.barra__campo-busqueda {
  border: none;
  margin: 1.4rem;
  outline: none;
  background-color: transparent;
}
.barra__icono-busqueda {
  padding: 1.6rem;
  margin-right: 0.7rem;
  background-color: #FF385C;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
}
.barra__icono-busqueda svg {
  position: relative;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translate(-50%, -50%);
}

.formulario {
  background-color: #FFFFFF;
  margin: 5.4rem auto 2rem auto;
  border-radius: 5rem;
  box-shadow: 0 3px 7px 0 #686868;
  padding: 3rem 0;
}
@media (min-width: 1024px) {
  .formulario {
    display: flex;
    justify-content: space-between;
    max-width: 90rem;
    padding: 0;
  }
}
.formulario__campo {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .formulario__campo:nth-child(1) {
    padding-left: 4rem;
  }
}
.formulario__campo:hover {
  background-color: #F0F0F0;
  border-radius: 5rem;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.4);
}
.formulario__label {
  font-size: 1.2rem;
  font-weight: 700;
  text-align: left;
}
.formulario__input {
  margin-top: 0.5rem;
  border: none;
  width: 100%;
  color: #686868;
  outline: none;
  background-color: transparent;
}
.formulario__submit {
  display: block;
  height: 4rem;
  border: none;
  border-radius: 2rem;
  background-image: radial-gradient(circle, rgb(255, 56, 92) 0%, rgb(189, 30, 89) 100%);
  padding: 0 2rem;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.2rem;
}

.informacion {
  padding: 15rem 6rem;
  background-color: #FF385C;
  border-radius: 1.5rem;
  margin: 8rem auto 0 auto;
}
.informacion__texto {
  font-size: 4.5rem;
  font-weight: 700;
  color: #FFFFFF;
}
.informacion__enlace {
  padding: 1rem 3rem;
  border-radius: 1rem;
  background-color: #FF385C;
  border: 1px solid #FFFFFF;
  color: #FFFFFF;
  cursor: pointer;
}

.busqueda {
  background-image: url("../img/bosque.webp");
  background-size: cover;
  background-position: center;
  height: 80rem;
  width: 80%;
  margin: -35rem auto 5rem auto;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding-bottom: 11rem;
}
.busqueda div {
  position: relative;
}
.busqueda div p {
  color: #FFFFFF;
  font-size: 5rem;
  text-align: center;
  font-weight: bold;
}
.busqueda div a {
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  padding: 1.7rem 4rem;
  background-color: #FFFFFF;
  font-size: 2.5rem;
  font-weight: bold;
  border-radius: 15rem;
  align-items: center;
}
.busqueda div a span {
  background-image: linear-gradient(90deg, #6F019C 0%, #C6017E 135.12%);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}